@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600);
:root {
  --main-bg-color: #595959;
}
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  overflow: hidden;
}

  .App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar {
    height: 70px;
    width:100%;
    border-bottom: solid 0.4px lightgray;
    padding-right: 2rem;
    display: flex;
    flex-wrap: nowrap;
}
.logo {
    float: left;
    padding: 1.6rem;
    padding-left: 40px;
    flex-shrink: 0;
}
.cartIcon {
    margin-right: 10px;
    margin-left: -10px;
    padding-bottom: 5px;
    width: 21.7px;

}

.button:focus { outline: none; color: var(--main-bg-color);}

.button {
    color: var(--main-bg-color);
    display: inline-block;
    display: flex;
    align-items: center;
}

.item {
    padding: 1.1rem;
    padding-top: 26px;
    float: right;
    font-family: "Poppins";
    font-weight: 600;
    font-size: 12px;
    line-height: 19px;
    border: none;
    border-bottom: solid 0.4px lightgray;
    color: var(--main-bg-color);
}
.cartSection {
    padding: 1.1rem;
    padding-top: 10px;
    float: right;
    font-family: "Poppins";
    font-weight: 600;
    font-size: 12px;
    line-height: 19px;
    border: none;
    border-bottom: solid 0.4px lightgray;
    color: var(--main-bg-color);
}
.items {
    width: 100%;
    float: right;
    padding-right: 40px;
    display: flex;
    justify-content: flex-end;
    margin-right: 0;
}

@media only screen and (max-width: 1175px) {
    .cartSection {
        padding-top: 25px;
    }
}
.cartitem {
    position: relative;
    height: 70;
    display: flex;
    display: inline-block;
    padding-bottom: 1rem;
}
.span {
    font-weight: 500;
    font-size: 10px;
    color: dark;
    margin-top: 0;
    bottom: 1;
    position: absolute;
    color: grey;
}
.span:hover {
    border-bottom: solid 1px grey;
}

.priceTag {
    font-size: 13px;
    align-self: flex-start;
    margin-top: 0;
    font-weight: 500;
    text-align: right;
}
h3 {
    font-weight: 200;
    font-size: 12px;
}
.smallimg {
    float: left;
    width: 100;
    height: auto;
    padding-left: 0;
}
.box {
    text-align: left;
    align-self: flex-start;
    height: 70px;
    padding-left: 1rem;
}
.amount {
    font-weight: 600;
    font-size: 12px;
}

hr {
    border-top: 0.2px solid rgba(237, 236, 224, 0.53);
    width: 80%;
    margin-right: 1rem;
}

.button {
    color: rgba(35, 121, 88, 0.75);
    align-items: center;
}
.cart {
    width: 275px;
